import React, { Component } from 'react';
import { Row, Col, Button, Form, Spinner } from 'react-bootstrap';
import { motion } from "framer-motion";
import { debounce } from "lodash";
import Select from 'react-select';

import { _getAllArtforms, _getAllArtists, _getAllOrganizations } from '../../services/axios/api';
import ArtistCard from '../../components/ArtistCard';
import BottomSlider from '../../components/uiElements/BottomSlider';
import Dropdown from '../../components/uiElements/Dropdown';
import { animateConfigs, helpers } from '../../constants';
import { SearchIcon, LocationIcon, SortIcon, ArtformIcon, SwitchIcon } from '../../components/svgComponents';
import reset from '../../assets/images/refresh-ccw.svg';
import classes from './artist.module.css';
import Loading from '../../components/uiElements/Loading';
import jsonData from '../../constants/states.json';
import globalClasses from '../../constants/globalStyles.module.css';


const stateOptions = [{
    "label" :"All Locations",
    "value" :"All Locations"
}, ...jsonData.states];

const statusOptions = [
    { value: -1, label: 'All Status' },
    { value: 0, label: 'Active Only' },
    { value: 1, label: 'Deactivated Only' }
]

const sortOptions = [ 
    { value: 'All', label: 'All' }, 
    { value: 'Newest First', label: 'Newest First' }, 
    { value: 'Oldest First', label: 'Oldest First' }, 
    { value: 'Name (A-Z)', label: 'Name (A-Z)' },
    { value: 'Name (Z-A)', label: 'Name (Z-A)' }
]

const customStyle = {
    valueContainer: () => ({height:42, paddingLeft:10, border:'none'}),
    placeholder: () => ({color:'#99A3B7', marginTop:7, border:'none'}), 
    control: (props) => ({...props, border:'none', boxShadow: '0 1px 1px rgb(0 0 0 / 10%), 0 2px 3px rgb(0 0 0 / 9%)', borderRadius:7}), 
    indicatorSeparator: () => ({width:0}),
}

export default class Artists extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            allOrganizations: [],
            allArtists: [],
            activeTab:'Artists',
            stateIndex:"",
            artistSearchKeyword: "",
            organizationSearchKeyword: "",
            searchLoading: false,
            OrgGetParams: {
                sortBy: 'createdAt',
                sort: -1
            },
            ArtistGetParams: {
                sortBy: 'createdAt',
                sort: -1
            }
        }; 
        this.artformOptions = [{ value: 'All Artforms', label: 'All Artforms' }];
    }

    componentDidMount() {

        helpers.clearLocalStorage();

        this.getArtists();
        this.getOrganizations();
        this.getAllArtforms();

    }

    shouldComponentUpdate() {
        if(this.props.history.location.pathname === '/' || this.props.history.location.pathname === '/organization') {
            return true;
        }
        return false;        
    }

    handleReset = () => {
       
        if(this.props.history.location.pathname === '/') {
            this.setState({artistSearchKeyword:"", stateIndex:""});
            this.getArtists();
            
        } else {
            this.setState({organizationSearchKeyword:""});
            this.getOrganizations();
        }        
    }

    renderArtists = () => {
        return this.state.allArtists.map((artist) => {
            const date = new Date(artist.createdAt);
            const parsedDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
            return <Col key={artist._id} lg={3}>
                <ArtistCard
                    image={artist?.image}
                    name={artist?.name}
                    date={parsedDate}
                    showBadge={true}
                    status={artist?.status ? "In Active" : "Active"}
                    bottomLeftTitle={
                    <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                        <div><LocationIcon size={20} color={'rgba(0, 25, 76, 0.4)'}/></div>
                        <div style={{opacity: 0.8, marginLeft: "2px", whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis", width: "100px"}}>
                            {artist?.state}
                        </div>
                    </div>}
                    onClick={() => this.props.history.push(`/viewartist/${artist._id}`)}
                />
            </Col>
        })
    }

    renderOrganizations = () => {
        return this.state.allOrganizations.map((organization) => {
            const date = new Date(organization.createdAt);
            const parsedDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`
            return <Col key={organization._id} lg={3}>
                <ArtistCard
                    image={organization?.image}
                    name={organization?.name}
                    date={parsedDate}
                    showBadge={false}
                    bottomLeftTitle={
                    <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                        <div><LocationIcon size={20} color={'rgba(0, 25, 76, 0.4)'}/></div>
                        <div style={{opacity: 0.8, marginLeft: "2px", whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis", width: "100px"}}>{organization?.state}</div>
                    </div>}
                    onClick={() => this.props.history.push(`/vieworganization/${organization._id}`)}
                />
            </Col>
        })
    }

    handleSearch = (type, keyword) => {
        this.setState({searchLoading: true});
        if(type === 'organization') {
            this.setState({organizationSearchKeyword: keyword});
            const params = {...this.state.OrgGetParams};
            params['search'] = keyword;
            this.debouncedOrganizationSearch(params);
        } else {
            this.setState({artistSearchKeyword: keyword});
            const params = {...this.state.ArtistGetParams};
            params['search'] = keyword;
            this.debouncedArtistSearch(params);
        }
    }
    
    getAllArtforms = () => {
        _getAllArtforms()
        .then(({ data: { artforms } }) => {
            artforms.map(artF => this.artformOptions.push({ value: artF._id, label: artF.name }));
        })
    }

    getArtists = () => {
        const params = {...this.state.ArtistGetParams}; 
        _getAllArtists(params)
        .then(({ data : { artists } }) => {
            console.log(artists, 'art');
            this.setState({allArtists:artists, loading:false});
        })
        .catch(err => {
            console.log(err);
            this.setState({loading:false});
        })
    }

    getOrganizations = () => {
        const params = {...this.state.OrgGetParams};
        _getAllOrganizations(params)
        .then(({ data : { organizations }}) => {
            console.log(organizations);
            this.setState({allOrganizations:organizations, loading:false});
        })
        .catch(err => {
            console.log(err);
            this.setState({loading:false});
        })
    }

    debouncedArtistSearch = debounce((params) => {

        if(params['search'] === "") {
            delete params['search']
        }

        _getAllArtists(params)
        .then(res => {
            console.log(res);
            this.setState({allArtists:res.data.artists, searchLoading:false});
        })
        .catch(err => {
            console.log(err);
            this.setState({searchLoading:false});
        })
    },500);

    debouncedOrganizationSearch = debounce((params) => {

        if(params['search'] === "") {
            delete params['search']
        }

        _getAllOrganizations(params)
        .then(res => {
            console.log(res);
            this.setState({allOrganizations:res.data.organizations, searchLoading:false});
        })
        .catch(err => {
            console.log(err);
            this.setState({searchLoading:false});
        })
    },500);

    onDropdownChange = (type, e) => {

        const { OrgGetParams, ArtistGetParams } = this.state;
        const { pathname } = this.props.history.location;

        const artistParams = {...ArtistGetParams};
        const orgParams = {...OrgGetParams};
        const params = pathname === '/' ? artistParams : orgParams;
        

        if(type === 'sort') {
            if(e.label === 'Newest First' || helpers.getOptionIndex(e.value, sortOptions) === 0) {
                params['sortBy'] = 'createdAt';
                params['sort'] = -1;
            }
            if(e.label === 'Oldest First') {
                params['sortBy'] = 'createdAt';
                params['sort'] = 1;
            }
            if(e.label === 'Name (A-Z)') {
                params['sortBy'] = 'name';
                params['sort'] = 1;
            }
            if(e.label === 'Name (Z-A)') {
                params['sortBy'] = 'name';
                params['sort'] = -1;
            }
        }  

        if(type === 'state') {
            this.setState({stateIndex: helpers.getOptionIndex(e.value, stateOptions)});
            if(helpers.getOptionIndex(e.value, stateOptions) === 0) {
                delete params['state'];
            } else {
                params['state'] = e.value;
            }
        }

        this.setState({searchLoading: true});

        if(this.props.history.location.pathname === '/') {
            if(type === 'status') {
                if(helpers.getOptionIndex(e.value, statusOptions) === 0) {
                    delete params['status'];
                } else {
                    params['status'] = e.value;
                }
            }
            if(type === 'artform') {
                if(helpers.getOptionIndex(e.value, this.artformOptions) === 0) {
                    delete params['artform'];
                } else {
                    params['artform'] = e.value;
                }
            }
            this.setState({ArtistGetParams:params});
            this.debouncedArtistSearch(params);
        } else {
            this.setState({OrgGetParams:params});
            this.debouncedOrganizationSearch(params);
        }
        
    }

    render() {

        if(this.state.loading) {
            return <Loading/>;
        }

        const { allArtists, allOrganizations, stateIndex } = this.state;
        const { pathname } = this.props.history.location;

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}>
                    <div>
                        <div style={{display:'flex', paddingBottom:5}}>
                            <div onClick={() => this.props.history.push('/')} className={classes.tabHeading} style={{opacity: pathname === '/' ? 1 : 0.7, marginLeft: 50}}>
                                Artists ({allArtists.length})
                            </div>
                            <div onClick={() => this.props.history.push('/organization')} className={classes.tabHeading} style={{opacity: pathname === '/organization' ? 1 : 0.7, marginLeft: 70}}>
                                Organizations ({allOrganizations.length})
                            </div>
                        </div>
                        <BottomSlider barValue={pathname === '/' ? 0 : pathname === '/organization' ? 350 : 0}/>
                    </div>
                    <div>
                        <Button onClick={() => this.props.history.push('/createOrganization')} variant="outline-primary" className={globalClasses.outlinePrimaryBtn}>
                            New Organization
                        </Button>
                        <Button onClick={() => this.props.history.push('/createartist')} style={{width:125, height:40, marginLeft:10}} className={globalClasses.primaryBtn}>
                            New Artist
                        </Button>
                    </div>
                </div>
                <div style={{marginTop:40}}>
                    <Row style={{marginBottom:20}}>
                        <Col lg={10} md={10} sm={10}>
                            <div style={{position:'relative'}}>
                                <Form.Control 
                                    onChange={(e) => this.handleSearch(pathname === '/' ? 'artist' : 'organization', e.target.value)} 
                                    value={pathname === '/' ? this.state.artistSearchKeyword : this.state.organizationSearchKeyword} 
                                    type="text" 
                                    placeholder={pathname === '/' ? 'Search Artists' : 'Search Organizations'}  
                                    style={{paddingLeft:40, paddingTop:8, paddingBottom:8}} 
                                />
                                <span className={classes.searchIcon}>
                                    <SearchIcon color="#BFC5D2" size={24}/>
                                </span>
                                {this.state.searchLoading && 
                                <span style={{position:'absolute', top:10, right:20}} >
                                    <Spinner style={{height:25, width:25}} animation="border" />
                                </span>}
                            </div>
                        </Col>
                        <Col onClick={() => window.location.reload()} style={{marginTop:10, fontWeight:600, cursor:'pointer', color:'#0052FF'}}>
                            Reset filters 
                            <img style={{marginLeft:10}} src={reset} alt="reset"/>
                        </Col>
                    </Row>
                    <Row style={{marginBottom:30}}>
                        <Col lg={3}>
                            <Select
                                styles={customStyle}
                                className="basic-single"
                                placeholder={
                                    <div style={{display:'flex', alignItems:'center'}}>
                                        <span style={{marginRight:6, marginTop:-2}}>
                                            <LocationIcon size={20} color="#99A3B7"/>
                                        </span>
                                        Location
                                    </div>}
                                isClearable={false}
                                isSearchable={false}
                                name="color"
                                isRtl={false}
                                onChange={(e) => this.onDropdownChange('state', e)}
                                // value={stateOptions[!stateIndex ? stateIndex : -1]}
                                options={stateOptions}
                            />
                        </Col>
                        { pathname === '/' ? <>
                        <Col lg={3}>
                            <Select
                                styles={customStyle}
                                className="basic-single"
                                placeholder={
                                    <div style={{display:'flex', alignItems:'center'}}>
                                        <span style={{marginRight:6, marginTop:-2}}>
                                            <SwitchIcon size={20} color="#99A3B7"/>
                                        </span>
                                        Status
                                    </div>}
                                isClearable={false}
                                isSearchable={false}
                                name="color"
                                isRtl={false}
                                onChange={(e) => this.onDropdownChange('status', e)}
                                // value={""}
                                options={statusOptions}
                            />  
                        </Col>
                        <Col lg={3}>
                            <Select
                                styles={customStyle}
                                className="basic-single"
                                placeholder={
                                    <div style={{display:'flex', alignItems:'center'}}>
                                        <span style={{marginRight:6, marginTop:-2}}>
                                            <ArtformIcon size={20} color="#99A3B7"/>
                                        </span>
                                        Artform
                                    </div>}
                                isClearable={false}
                                isSearchable={false}
                                name="color"
                                isRtl={false}
                                onChange={(e) => this.onDropdownChange('artform', e)}
                                // value={""}
                                options={this.artformOptions}
                            />  
                        </Col>
                        </> : null}
                        <Col lg={3}>
                            <Select
                                styles={customStyle}
                                className="basic-single"
                                placeholder={
                                    <div style={{display:'flex', alignItems:'center'}}>
                                        <span style={{marginRight:6, marginTop:-2}}>
                                            <SortIcon size={20} color="#99A3B7"/>
                                        </span>
                                        Sort By
                                    </div>}
                                isClearable={false}
                                isSearchable={false}
                                name="color"
                                isRtl={false}
                                onChange={(e) => this.onDropdownChange('sort', e)}
                                // value={""}
                                options={sortOptions}
                            />  
                        </Col>
                    </Row>
                </div>
                <div>
                    <Row>
                        {pathname === '/' ? this.renderArtists() : pathname === '/organization' ? this.renderOrganizations() : this.renderArtists()}            
                    </Row>
                </div>
            </motion.div>
        )
    }
}
