import React, { useState } from 'react';
import { Row, Col, Button, Form, Spinner } from 'react-bootstrap';

import { setToken } from '../../routes/authUtilities';
import { _sendOtp, _verifyOtp } from '../../services/axios/api';
import logo from '../../assets/images/logoLarge.svg';
import globalClasses from '../../constants/globalStyles.module.css'

export default function Login(props) {

    const [currentInput, setCurrentInput] = useState("email");
    const [loading, setLoading] = useState(false);
    const [email, setEmail] = useState("");
    const [otp, setOtp] = useState("");


    function handleVerifyOtp() {

        setLoading(true);

        _verifyOtp({
            email,
            otp
        })
        .then(({ data: { token } }) => {
            setToken(token);
            localStorage.setItem("adminEmail", email);
            props.history.push("/");
        })
        .catch(err => {
            setLoading(false);
            alert(err.response.data.error);
        });

    }

    function handleOtpSend() {

        setLoading(true);
        _sendOtp({email})
        .then(res => {
            setLoading(false);
            setCurrentInput("otp");
        })
        .catch(err => {
            setLoading(false);
            alert(err.response.data.error);
        });
        
    }

    function emailComponent() {
        return <>
            <Form.Control
                type="text" 
                placeholder="Enter your email" 
                style={{width:'45%', margin: '0 auto'}}
                onChange={(e) => setEmail(e.target.value)}
                onKeyDown={(e) => e.key === 'Enter' && handleOtpSend()} 
                value={email} 
            />
            <Button onClick={handleOtpSend} style={{width:'45%', height:40, marginTop:15}} className={globalClasses.primaryBtn}>
                {loading ? 
                <Spinner color='#fff' style={{height:20, width:20, marginTop:2}} animation="border" /> :
                "Send Otp"}
            </Button>
        </>
    }

    function otpVerifyComponent() {
        return <>
            <div style={{marginBottom:10}}>Otp sent to <b>{email}</b></div>
            <Form.Control
                type="text" 
                placeholder="Enter your otp" 
                style={{width:'45%', margin: '0 auto'}}
                onChange={(e) => setOtp(e.target.value)} 
                onKeyDown={(e) => e.key === 'Enter' && handleVerifyOtp()}
                value={otp} 
            />
            <Row style={{width:'46.5%', margin: '0 auto'}}>
                <Col>
                    <Button variant='outline-secondary' onClick={() => setCurrentInput("email")} style={{width:'100%', height:40, marginTop:15}}>
                        Edit Email
                    </Button>
                </Col>
                <Col>
                    <Button onClick={handleVerifyOtp} style={{width:'100%', height:40, marginTop:15}} className={globalClasses.primaryBtn}>
                        {loading ? 
                        <Spinner color='#fff' style={{height:20, width:20, marginTop:2}} animation="border" /> :
                        "Login"}
                    </Button>
                </Col>
            </Row>

        </>
    }

    return (
        <div style={{textAlign:'center', marginTop:'9%'}}>
            <img style={{height:80, marginBottom:30}} src={logo} alt="logo"/>
            {currentInput === "email" ? emailComponent() : otpVerifyComponent()}
        </div>
    )
}
