import React from 'react';
import { motion } from "framer-motion";
import PropTypes from 'prop-types';

import classes from '../../constants/globalStyles.module.css';

export default function BottomSlider({ barValue, animateDuration, onClick, barWidth }) {
    return (
        <div onClick={onClick} style={{ width:barWidth }} className={classes.slideBarContainer}>
            <motion.div 
                style={{ width:barWidth/2 }}
                className={classes.slideBar}
                animate={{ x: barValue/2 }}
                transition={{ duration:animateDuration }}
            />
        </div>
    )
}

BottomSlider.defaultProps = {
    barValue:0,
    animateDuration: 0.5,
    barWidth: 350
}; 

BottomSlider.propTypes = {
    barValue: PropTypes.number,
    animateDuration: PropTypes.number,
    barWidth: PropTypes.number,
    onClick: PropTypes.func
}; 
