import React, { Component, useContext } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { motion } from "framer-motion";
import Select from 'react-select';

import ArtistCard from '../../components/ArtistCard';
import Dropdown from '../../components/uiElements/Dropdown';
import { animateConfigs, colors, helpers } from '../../constants';
import { WalletContext } from '../../App';
import { SearchIcon, LocationIcon, SortIcon, ArtformIcon, SwitchIcon, Wallet, ChevronDown } from '../../components/svgComponents';
import classes from '../artists/artist.module.css';
import jsonData from '../../constants/states.json';
import globalClasses from '../../constants/globalStyles.module.css';
import Loading from '../../components/uiElements/Loading';
import NearHelperFunctions from '../../services/nearHelperFunctions';


const statusOptions = [
    { value: -1, label: 'All Status' },
    { value: 0, label: 'Listed Only' },
    { value: 1, label: 'Unlisted Only' }
]

const sortOptions = [ 
    { value: 'All', label: 'All' }, 
    { value: 'Newest First', label: 'Newest First' }, 
    { value: 'Oldest First', label: 'Oldest First' }
]

const customStyle = {
    valueContainer: () => ({height:42, paddingLeft:10, border:'none'}),
    placeholder: () => ({color:'#99A3B7', marginTop:7, border:'none'}), 
    control: (props) => ({...props, border:'none', boxShadow: '0 1px 1px rgb(0 0 0 / 10%), 0 2px 3px rgb(0 0 0 / 9%)', borderRadius:7}), 
    indicatorSeparator: () => ({width:0}),
}


class Nft extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            nfts: [],
            filterData: [],
            filters: {
                status:"All Status",
                sort:"All"
            },
            activeTab: 'Artists'
        }; 
    }

    componentDidMount() {

        helpers.clearLocalStorage();

        if(this.props.contextValue.wallet) {
            this.fetchNfts();
        }

    }

    componentDidUpdate(prevProps, prevState) {

        if(prevProps.contextValue.wallet !== this.props.contextValue.wallet) {
            this.fetchNfts();
        }
        
        if(prevState.filters !== this.state.filters) {
            this.applyFilters();
        }

    }

    fetchNfts = async () => {
        
        const functions = new NearHelperFunctions(this.props.contextValue); 
        
        functions.getAllNfts()
        .then(res => {
            const reversedAllNfts = [...res];
            const reversedFilterNfts = [...res];
            this.setState({nfts:reversedAllNfts.reverse(), filterData:reversedFilterNfts.reverse(), loading:false});
        })
        .catch(err => {
            console.log(err);
            alert("something went wrong!");
            this.setState({loading:false});
        });

    }


    renderNfts = () => {
        return this.state.nfts.map((nft, index) => {
            const date = new Date(nft.metadata.issued_at);
            const parsedDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`;
            return <Col key={index} lg={3}>
                <ArtistCard
                    image={nft.metadata.media}
                    name={nft.metadata.title ? nft.metadata.title : "No title here"}
                    date={parsedDate}
                    showBadge={true}
                    status={nft.listed ? "Listed" : "Unlisted"}
                    bottomLeftTitle={
                        <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                            <div style={{marginTop:-3}}><Wallet size={15} color={colors.inActiveIcon}/></div>
                            <div style={{opacity:0.8, marginLeft:2}}>{nft.price}</div>
                        </div>}
                    onClick={() => this.props.history.push(`/viewnft/${nft.token_id}`, nft)}
                />
        </Col>
        })
    }

    applyFilters = () => {
        
        const { status, sort } = this.state.filters;
        const { filterData } = this.state;
        const copiedFilterArr = [...filterData];
        let result;

        if((status === "All Status" && sort === "All") || (status === "All Status" && sort === "Newest First")) {
            result = copiedFilterArr;
        } else if((status === "Listed Only" && sort === "All") || (status === "Listed Only" && sort === "Newest First")) {
            result = copiedFilterArr.filter(item => item.listed);
        } else if((status === "Unlisted Only" && sort === "All") || (status === "Unlisted Only" && sort === "Newest First")) {
            result = copiedFilterArr.filter(item => !item.listed);
        } else if(status === "All Status" && sort === "Oldest First") {
            result = copiedFilterArr.reverse();
        } else if(status === "Listed Only" && sort === "Oldest First") {
            const listedOnly = copiedFilterArr.filter(item => item.listed);
            result = listedOnly.reverse();            
        } else if(status === "Unlisted Only" && sort === "Oldest First") {
            const unListedOnly = copiedFilterArr.filter(item => !item.listed);
            result = unListedOnly.reverse(); 
        }

        this.setState({nfts:result});
    }

    onDropdownChange = (type, e) => {
        if(type === "sort") {
            this.setState(prevState => ({
                ...prevState,
                filters: {
                    ...prevState.filters,
                    sort:e.label
                }
            }));
        } else {
            this.setState(prevState => ({
                ...prevState,
                filters: {
                    ...prevState.filters,
                    status:e.label
                }
            }));
        }
    }

    render() {

        const { loading } = this.state;

        if(loading) {
            return <Loading/>;
        }

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <WalletContext.Consumer>
                    {contextValue => {
                        return <>
                        <div className={globalClasses.flexRowSpace}>
                            <div style={{fontWeight:600, fontSize:22}}>
                                Manage NFTs (25)
                            </div>
                            <div>
                                <Button 
                                    onClick={() => contextValue.isWalletSignedIn ? this.props.history.push('/createnft') : alert("Connect your wallet to create nft")} 
                                    style={{width:125, height:40, marginLeft:10, opacity: contextValue.isWalletSignedIn ? 1 : 0.6}} 
                                    className={globalClasses.primaryBtn}
                                >
                                    New NFT
                                </Button>
                            </div>
                        </div>
                        <div style={{marginTop:40}}>
                        <Row style={{marginBottom:30}}>
                            <Col lg={3}>
                                <Select
                                    styles={customStyle}
                                    className="basic-single"
                                    placeholder={
                                        <div style={{display:'flex', alignItems:'center'}}>
                                            <span style={{marginRight:6, marginTop:-2}}>
                                                <SwitchIcon size={20} color="#99A3B7"/>
                                            </span>
                                            Status
                                        </div>}
                                    isClearable={false}
                                    isSearchable={false}
                                    name="color"
                                    isRtl={false}
                                    onChange={(e) => this.onDropdownChange('status', e)}
                                    // value={""}
                                    options={statusOptions}
                                />  
                            </Col>
                            <Col lg={3}>
                                <Select
                                    styles={customStyle}
                                    className="basic-single"
                                    placeholder={
                                        <div style={{display:'flex', alignItems:'center'}}>
                                            <span style={{marginRight:6, marginTop:-2}}>
                                                <SortIcon size={20} color="#99A3B7"/>
                                            </span>
                                            Sort By
                                        </div>}
                                    isClearable={false}
                                    isSearchable={false}
                                    name="color"
                                    isRtl={false}
                                    onChange={(e) => this.onDropdownChange('sort', e)}
                                    // value={""}
                                    options={sortOptions}
                                />  
                            </Col>
                        </Row>
                        </div>
                        <div>
                            <Row>
                                {this.renderNfts()}            
                            </Row>
                        </div>
                    </>}}
                </WalletContext.Consumer>
            </motion.div>
        )
    }
}

export default function NftWrapper(props) {

    const contextValue = useContext(WalletContext);

    return <Nft
        contextValue={contextValue}
        history={props.history}
    />;
}