import React from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import Select from 'react-select';

import pdf from '../assets/images/pdf.png';
import doc from '../assets/images/doc.png';
import video from '../assets/images/video.png';
import classes from '../pages/artists/artist.module.css';
import globalClasses from '../constants/globalStyles.module.css';
import { Plus, Minus, NearSvg, Delete } from './svgComponents';
import { helpers } from '../constants';

export function NftForm(props) {

    const { 
        onDropdownChange,
        listOn,
        price,
        listInMarketPlace,
        quantity,
        artist,
        artistName,
        materialMediumUsed,
        description,
        name,
        onChangeText,
        modifyInput,
        customInputs
    } = props;

    const options = [
        { value: 'strawberry', label: 'Strawberry' },
        { value: 'vanilla', label: 'Vanilla' }
      ]

    return ( 
        <div>
            <div className={globalClasses.containerCard}>
                <Row style={{marginBottom:20}}>
                    <Col>
                        <Form.Label className={globalClasses.label}>
                            Name<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <Form.Control value={name} onChange={(e) => onChangeText("name", e.target.value)} type="text" placeholder="Type here" />
                    </Col>
                </Row>
                <Row style={{marginBottom:20}}>
                    <Col>
                        <div className={globalClasses.flexRowSpace}>
                            <Form.Label className={globalClasses.label}>
                                Description<span style={{color:'#FF4848'}}>*</span>  
                            </Form.Label>
                            <div style={{fontSize:14, opacity:0.6}}>
                                {description.length}/400
                            </div>
                        </div>
                        <Form.Control value={description} onChange={(e) => onChangeText("description", e.target.value)} as="textarea" style={{height:125}} placeholder="Type here" />
                    </Col>
                </Row>
                {/* <Row style={{marginBottom:20}}>
                    <Col>
                        <Form.Label className={globalClasses.label}>
                            Material/Medium used<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <Form.Control value={materialMediumUsed} onChange={(e) => onChangeText("materialMediumUsed", e.target.value)} type="text" placeholder="Type here" />
                    </Col>
                </Row> */}
                <Row style={{marginBottom:20}}>
                    <Col>
                        <Form.Label style={{opacity:0.7}} className={globalClasses.label}>
                            Artist*
                        </Form.Label>
                        <Form.Control 
                            value={artistName} 
                            type="text"
                            disabled={true} 
                        />
                    </Col>
                    <Col>
                        <Form.Label className={globalClasses.label}>
                            Material/Medium used<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <Form.Control value={materialMediumUsed} onChange={(e) => onChangeText("materialMediumUsed", e.target.value)} type="text" placeholder="Type here" />
                    </Col>
                    {/* <Col>
                        <Form.Label className={globalClasses.label}>
                            Quantity (Max 10)<span style={{color:'#FF4848'}}>*</span>
                        </Form.Label>
                        <div className={globalClasses.flexRow}>
                            <div onClick={() => quantity > 1 ? onChangeText("quantity", quantity - 1) : null}>
                                <Minus size={25}/>
                            </div>  
                            <Form.Control 
                                type="text" 
                                value={quantity.toLocaleString('en-US', {minimumIntegerDigits: 2, useGrouping:false})}
                                style={{width:45, margin:'0px 10px'}} 
                                contentEditable={false}
                            />
                            <div onClick={() => quantity < 10 ? onChangeText("quantity", quantity + 1) : null}>
                                <Plus size={25}/>
                            </div>
                        </div>
                    </Col> */}
                </Row>
                <Row>
                    {customInputs.map((cI, i) => {
                    return <React.Fragment key={i}>
                        <Col lg={11}>
                            <Form.Label style={{textTransform:'capitalize'}} className={globalClasses.label}>
                                {cI.name}
                            </Form.Label>
                            {cI.type === 0 ?
                            <Form.Control type="text" placeholder="Type here" onChange={(e) => modifyInput('edit', i, e.target.value)} value={cI.text}/> :
                            cI.type === 1 ?
                            <div style={{display:'flex', alignItems:'center'}}>
                                <label htmlFor={`UploadImg${i}`} style={{width:150, height:40, marginRight:15, cursor:'pointer', borderRadius:5}} className={globalClasses.primaryBtn}>
                                    <div style={{color:'white', textAlign:'center', marginTop:7.2, fontSize:15, textTransform:'capitalize'}}>
                                        {`${cI.fileType} Upload`}
                                    </div>
                                    <input 
                                        onChange={(e) => modifyInput('editFile', i, e)} 
                                        id={`UploadImg${i}`} 
                                        hidden 
                                        type="file" 
                                        name="Pick an Image" 
                                        accept={helpers.acceptedFormat(cI.fileType)}
                                    />
                                </label>
                                <div>
                                    <div style={{display:'flex', marginRight:10}}>
                                        {cI.fileType === "video" && cI.fileKey !== "fileKey" ?
                                        <div className={globalClasses.flexRow}>
                                            <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='100px' width= '100px' style={{borderRadius:10, marginLeft:-10, cursor:'pointer'}} src={video} alt="upload"/>
                                            <div className={globalClasses.truncate} style={{width:200, marginLeft:-10}}>{cI.fileKey.name}</div>
                                        </div> :
                                        cI.fileType === "image" && cI.fileKey !== "fileKey"  ?
                                        <div className={globalClasses.flexRow}>
                                            <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{cursor:'pointer', objectFit:'contain'}} src={cI.uri ? cI.uri : cI.fileUrl} alt="upload"/>
                                            <div className={globalClasses.truncate} style={{width:200, marginLeft:15}}>{cI.fileKey.name}</div>
                                        </div> :
                                        cI.fileType === "document" && cI.fileKey !== "fileKey"  ?
                                        <div className={globalClasses.flexRow}>
                                            <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={doc} alt="upload"/>
                                            <div className={globalClasses.truncate} style={{width:200}}>{cI.fileKey.name}</div>
                                        </div> :
                                        cI.fileType === "pdf" && cI.fileKey !== "fileKey"  ?
                                        <div className={globalClasses.flexRow}>
                                            <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={pdf} alt="upload"/>
                                            <div className={globalClasses.truncate} style={{width:200}}>{cI.fileKey.name}</div>
                                        </div> : null} 
                                    </div> 
                                </div>
                            </div> :
                            <div>
                                <label className={classes.datePicker} htmlFor="dateStyle">
                                    <input id="dateStyle" type="date" value={cI.date} onChange={(e) => modifyInput('editDate', i, e.target.value)}/>
                                </label>
                            </div> }
                        </Col> 
                        <Col lg={1} onClick={() => modifyInput('delete', i)} style={{marginTop:35, marginBottom:40, cursor:'pointer'}}>
                            <Delete size={20}/>
                        </Col>
                    </React.Fragment>})}
                </Row>
            </div>
            {/* <div className={globalClasses.containerCard} style={{marginTop:15}}>
                <div style={{background: 'rgba(230, 238, 255, 0.8)', padding:'7px 15px', borderRadius:6, fontWeight:400, fontSize:18, marginBottom:15}}>
                    <Form.Check onChange={(e) => onChangeText("listInMarketPlace", !listInMarketPlace)} value={listInMarketPlace} type="checkbox" label="List in marketplace" />
                </div>
                <Row style={{marginBottom:20}}>
                    <Col lg={6} style={{marginBottom:20}}>
                        <Form.Label style={{opacity: listInMarketPlace ? 1 : 0.5}} className={globalClasses.label}>
                            Price for one item
                        </Form.Label>
                        <div className={globalClasses.flexRow}>
                            <Form.Control type="text" placeholder="0.0000" value={price} onChange={() => onChangeText("price", price)} disabled={!listInMarketPlace} />
                            <div style={{marginLeft:-30, opacity: listInMarketPlace ? 1 : 0.5}}>
                                <NearSvg/>
                            </div>
                        </div>
                    </Col>
                    <Col lg={6} style={{marginBottom:20}}>
                        <Form.Label style={{opacity: listInMarketPlace ? 1 : 0.5}} className={globalClasses.label}>
                            List on
                        </Form.Label>
                        <div>
                            <label style={{width:'100%'}} htmlFor="dateStyle">
                                <Form.Control 
                                    id="dateStyle" 
                                    type="date" 
                                    placeholder="0.0000" 
                                    value={listOn}
                                    onChange={(e) => onChangeText('listOn', e.target.value)}
                                    disabled={!listInMarketPlace} 
                                />
                            </label>
                        </div>
                    </Col>
                </Row>
                
            </div> */}
        </div>
    )
}

export default NftForm;
