import React, { Component } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import Select from 'react-select';
import { withAlert } from 'react-alert';
import { motion } from "framer-motion";

import { _getPresignedUrl, _uploadFileAws, _postArtist, _updateArtist, _getAllArtforms, _getAllOrganizations } from '../../services/axios/api';
import ArtistForm from '../../components/ArtistForm';
import Modal from '../../components/uiElements/Modal';
import { helpers, animateConfigs } from '../../constants';
import { EditIcon, Profile } from '../../components/svgComponents';
import Loading from '../../components/uiElements/Loading';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';
import jsonData from '../../constants/states.json';

class CreateArtist extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            image: null, 
            imageRaw: null, 
            fieldTypeIndex: "",
            fileFormatIndex: "",
            stateIndex: "",
            fieldTitle: "",
            customInputs: [],
            name: "",
            description: "",
            city:"",
            wallet: "",
            phone: "",
            email: "",
            website: "",
            facebook: "",
            instagram: "",
            artformIndex: "",
            organizationIndex: "",
            emailValidateError: false,
            phoneValidateError: false,
            loading: false,
        }

        this.fieldTypeOptions = [
            { value: 'text', label: 'Text Field' },
            { value: 'file', label: 'File upload' },
            { value: 'date', label: 'Date picker' }
        ]

        this.fileFormatOptions = [
            { value: 'image', label: 'Image' },
            { value: 'video', label: 'Video' },
            { value: 'document', label: 'Document' },
            { value: 'pdf', label: 'PDF' }
        ]

        this.stateOptions = jsonData.states;
        this.artformOptions = [];
        this.organizationOptions = [];
    }

    componentDidMount() {

        // console.log(states);

        this.getDropdownData();

    }

    getDropdownData = () => {

        const params = {
            sortBy: 'createdAt',
            sort: -1
        }

        _getAllArtforms()
        .then(({ data: { artforms } }) => {
            artforms.map(artF => this.artformOptions.push({ value: artF._id, label: artF.name }));
            
            _getAllOrganizations(params)
            .then(({ data: { organizations } }) => {
                organizations.map(org => this.organizationOptions.push({ value: org._id, label: org.name }));
                if(this.props.location?.state) {
                    this.setRequiredData();
                }
            })

        })
    }

    setRequiredData = () => {

        const { state } = this.props.location;
        const artistKeys = Object.entries(state);
        const stateKeys = Object.keys(this.state);

        artistKeys.map(key => {
            if(stateKeys.includes(key[0])) {
                this.setState({[key[0]]: key[1]});
            }
            // else if(key[0] === 'city') {
            //     const index = this.cityOptions.findIndex(city => city.value == key[1]);
            //     this.setState({cityIndex: index});
            // }
            else if(key[0] === 'state') {
                const index = this.stateOptions.findIndex(state => state.value == key[1]);
                this.setState({stateIndex: index}); 
            } 
            else if(key[0] === 'artform') {
                const index = this.artformOptions.findIndex(artform => artform.value == key[1]._id);
                this.setState({artformIndex: index}); 
            }
            else if(key[0] === 'organization') {
                console.log(this.organizationOptions, 'this.organizationOptions');
                console.log(state, 'state');
                const index = this.organizationOptions.findIndex(organization => organization.value == key[1]._id);
                this.setState({organizationIndex: index}); 
            }
            else if(key[0] === 'custom') {
                key[1].map(cI => delete cI._id);
                this.setState({customInputs: key[1]});
            }
        })

    }

    onDropdownChange = (type, e) => {
        if(type === 'fieldType') {
            this.setState({fieldTypeIndex:helpers.getOptionIndex(e.value, this.fieldTypeOptions)});
        } else if(type === 'fileFormat') {
            this.setState({fileFormatIndex:helpers.getOptionIndex(e.value, this.fileFormatOptions)});
        } else if(type === 'state') {
            this.setState({stateIndex:helpers.getOptionIndex(e.value, this.stateOptions)});
        } else if(type === 'organization') {
            this.setState({organizationIndex:helpers.getOptionIndex(e.value, this.organizationOptions)});
        } else if(type === 'artform') {
            this.setState({artformIndex:helpers.getOptionIndex(e.value, this.artformOptions)});
        }
    }

    handleAddCancelCustomField = (type) => {
        const { fieldTypeIndex, fileFormatIndex, fieldTitle, customInputs } = this.state;
        this.setState({showModal:false});
        if(type === 'add') {
            const copiedArr = [...customInputs];
            const data = {
                "name": fieldTitle
            }
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'text') {
                data["text"] = "";
                data["type"] = 0;
            }
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'file') {
                data["fileKey"] = "fileKey";
                data["fileType"] = this.fileFormatOptions[fileFormatIndex]?.value;
                data["type"] = 1;
            } 
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'date') {
                data["date"] = "";
                data["type"] = 2;
            }
            copiedArr.push(data);             
            this.setState({fieldTypeIndex: "", fileFormatIndex: "", fieldTitle: "", customInputs: copiedArr});
        } else {

        }
    }

    handleModifyInput = async (type, i, value) => {
        
        const copiedArr = [...this.state.customInputs];
        if(type === 'delete') {
            const updatedArr = this.state.customInputs.filter((_, index) => i !== index);
            this.setState({customInputs:updatedArr});
        } else if(type === 'editFile') {
            const uri = await helpers.readImage(value);
            copiedArr[i].fileKey = value.target.files[0];
            copiedArr[i].uri = uri;
            this.setState({customInputs:copiedArr});
        } else if(type === 'editDate') {
            copiedArr[i].date = value;
            this.setState({customInputs:copiedArr});
        }
         else {
            copiedArr[i].text = value;
            this.setState({customInputs:copiedArr});
        }

    }

    onChangeText = (stateName, value) => {
        this.setState({[stateName]: value});
    }
     
    handleImage = async (e) => {
        const res = await helpers.readImage(e);
        this.setState({image:res[0], imageRaw: e.target.files[0]});
    }

    buildImgTag = () => {
        return <div style={{display:'flex', marginRight:10}}>
            <img height='180px' width= '180px' style={{borderRadius:100, objectFit:'cover'}} src={this.state.image} alt="upload"/>
        </div>
    } 

    checkForError = () => {
        
        const email = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/;

        let phoneValidateError = false;
        let emailValidateError = false;

        if(this.state.phone) {
            if(this.state.phone.length < 10) {
                this.setState({phoneValidateError: true});
                phoneValidateError = true;
            } else {
                this.setState({phoneValidateError: false});
            }
        }
        if(this.state.email) {
            this.setState({emailValidateError: !email.test(this.state.email)});
            emailValidateError = !email.test(this.state.email);
        }
        return (phoneValidateError || emailValidateError);
    }
    
    handleArtistCreation = async () => {

        const error = this.checkForError();

        if(!error) {

            this.setState({loading:true});

            const { 
                imageRaw, 
                customInputs, 
                name, 
                description, 
                wallet,
                phone, 
                email, 
                website, 
                facebook, 
                instagram,
                stateIndex,
                city,
                artformIndex, 
                organizationIndex
            } = this.state;
    
            let data = {
                name: name
            }
    
            if(this.stateOptions[stateIndex]?.value) {
                data["state"] = this.stateOptions[stateIndex]?.value;
            }
 
            if(this.artformOptions[artformIndex]?.value) {
                data["artform"] = this.artformOptions[artformIndex]?.value;
            }
            if(this.organizationOptions[organizationIndex]?.value) {
                data["organization"] = this.organizationOptions[organizationIndex]?.value;
            }
    
            const stateObj = {phone:phone, email:email, website:website, facebook:facebook, instagram:instagram, wallet:wallet, description:description, city:city};
            const stateEntries =  Object.entries(stateObj);
    
            stateEntries.map(entry => {
                if(entry[1] !== "") {
                    data[entry[0]] = entry[1];
                }
            });
            
            if(imageRaw !== null) {
                const urlRes = await _getPresignedUrl({"module": "artist", "totalFiles": 1});
                const uploadRes = await _uploadFileAws(urlRes.data.urls[0].url, imageRaw, imageRaw.type);
                
                if(uploadRes.status === 200) {
                    data["image"] = urlRes.data.urls[0].Key
                }
            }
    
            const validatedArr = customInputs.filter(inp => inp.text !== "" && inp.fileKey !== "fileKey" && inp.date !== "");
    
            const onlyFiles = validatedArr.filter(inp => inp.type === 1 && inp.uri);

            if(onlyFiles.length >= 1) {
    
                const fileUrlRes = await _getPresignedUrl({"module": "artist", "totalFiles": onlyFiles.length});
                
                let j = 0;
    
                for (let i = 0; i < validatedArr.length; i++) {
                    if(validatedArr[i].type === 1 && validatedArr[i].uri) {
                        const uploadRes = await _uploadFileAws(fileUrlRes.data.urls[j].url, validatedArr[i].fileKey, validatedArr[i].fileKey.type);
                        if(uploadRes.status === 200) {
                            validatedArr[i]['fileKey'] = fileUrlRes.data.urls[j].Key;
                            j++;
                        }
                    }
                }

                const customArray = [];

                if(this.props.location?.state) {

                    validatedArr.map(item => {
                        if(item.uri) {
                            delete item['uri'];
                        }
                        if(item.fileUrl) {
                            delete item['fileUrl'];
                        }
                        customArray.push(item);
                    });

                    data["custom"] = customArray;

                    this.updateArtist(data);

                } else {

                    validatedArr.map(item => {
                        if(item.type !== 1 || (item.type === 1 && item.uri)) {
                            if(item.uri) {
                                delete item['uri'];
                            }
                            customArray.push(item);
                        }
                    });
    
                    data["custom"] = customArray;

                    this.addArtist(data);

                }
            } else {

                const customArray = [];
    
                if(this.props.location?.state) {
                    validatedArr.map(item => {
                        if(item.fileUrl) {
                            delete item['fileUrl'];
                        }
                        customArray.push(item);
                    });
                    
                    data["custom"] = validatedArr;

                    this.updateArtist(data);
                } else {
                    this.addArtist(data);
                }
            } 
               
        }
        
    }

    addArtist = (data) => {
        
        data['createdBy'] = 0;
        
        _postArtist(data)
        .then(res => {
            this.setState({loading:false});
            this.props.history.push('/');
            this.props.alert.success('Artist added successfully!', {timeout:2000});
        })
        .catch(err => {
            console.log(err.response.data.error, 'err');
            this.props.alert.error(err.response.data.error, {timeout:5000});
            this.setState({loading:false});
        })
    }

    updateArtist = (data) => {
        _updateArtist(this.props.location.state._id, data)
        .then(res => {
            this.setState({loading:false});
            this.props.history.push('/');
            this.props.alert.success('Artist updated successfully!', {timeout:2000});
        })
        .catch(err => {
            console.log(err.response.data, 'err');
            this.props.alert.error(err.response.data.error, {timeout:5000});
            this.setState({loading:false});
        })
    }

    customInput = () => {
        return <Row style={{marginBottom:20}}>
            <Col lg={12}>
                <Form.Label className={globalClasses.label}>
                    Field Type<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Select
                    styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                    className="basic-single"
                    classNamePrefix="select"
                    defaultValue={"Select"}
                    onChange={(e) => this.onDropdownChange('fieldType', e)}
                    value={this.fieldTypeOptions[this.state.fieldTypeIndex]}
                    isClearable={false}
                    isSearchable={false}
                    name="color"
                    options={this.fieldTypeOptions}
                />
            </Col>
            {this.fieldTypeOptions[this.state.fieldTypeIndex]?.value === "file" ? <Col lg={12} style={{marginTop:15}}>
                <Form.Label className={globalClasses.label}>
                    Upload Format<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Select
                    styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                    className="basic-single"
                    classNamePrefix="select"
                    defaultValue={"Select"}
                    onChange={(e) => this.onDropdownChange('fileFormat', e)}
                    value={this.fileFormatOptions[this.state.fileFormatIndex]}
                    isClearable={false}
                    isSearchable={false}
                    name="color"
                    options={this.fileFormatOptions}
                />
            </Col> : null}
            <Col lg={12} style={{marginTop:15}}>
                <Form.Label className={globalClasses.label}>
                    Field Title<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Form.Control type="text" placeholder="Type here" onChange={(e) => this.setState({fieldTitle: e.target.value})} value={this.state.fieldTitle} />
            </Col>
        </Row>
    }


    render() {

        if(this.state.loading) {
            return <Loading/>;
        }

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}> 
                    <div style={{fontWeight:600, fontSize:22}}>
                        {this.props.location?.state ? 'Edit' : 'New'} artist
                    </div>
                    <div style={{width:"25%", display:"flex"}}>
                        <Button onClick={() => this.props.history.goBack()} variant="outline-primary" style={{width:"100%"}} className={globalClasses.outlinePrimaryBtn}>
                            Cancel
                        </Button>
                        <Button onClick={this.state.name && this.state.image && this.state.wallet !== "" ? this.handleArtistCreation : null} style={{width:"100%", height:40, marginLeft:10, opacity: this.state.name && this.state.image && this.state.wallet !== "" ? 1 : 0.5}} className={globalClasses.primaryBtn}>
                            {this.props.location?.state ? 'Update Artist' : 'Create Artist'}
                        </Button>
                    </div>
                </div>
                <div style={{marginTop:25, display:'flex', justifyContent:'space-between'}}>
                    <div className={globalClasses.containerCard} style={{width:'28%', textAlign:'center', padding: "50px", height: "fit-content"}}>
                        <label htmlFor="addImg" style={{position:'relative', cursor:'pointer'}}>
                            <input onChange={(e) => this.handleImage(e)} id="addImg" hidden type="file" name="Pick an Image" accept="image/x-png,image/gif,image/jpeg"/>
                            {this.state.image ?
                                this.buildImgTag() :
                            <Profile/>}
                            <div className={classes.editIcon}>
                                <EditIcon size={20}/>
                            </div>
                        </label>
                        <div style={{fontSize:16, fontWeight:600, opacity:0.7, marginTop:15}}>Click to upload photo</div>
                    </div>
                    <div className={globalClasses.containerCard} style={{width:'70%'}}>
                        <ArtistForm
                            customInputs={this.state.customInputs}
                            modifyInput={this.handleModifyInput}
                            onChangeText={this.onChangeText}
                            name={this.state.name}
                            description={this.state.description}
                            wallet={this.state.wallet}
                            phoneNumber={this.state.phone}
                            email={this.state.email}
                            website={this.state.website}
                            facebook={this.state.facebook}
                            instagram={this.state.instagram}
                            stateOptions={this.stateOptions}
                            organizationOptions={this.organizationOptions}
                            artformOptions={this.artformOptions}
                            onDropdownChange={this.onDropdownChange} 
                            stateValue={this.stateOptions[this.state.stateIndex]}
                            city={this.state.city}
                            setCity={(value) => this.setState({city:value})}
                            artformValue={this.artformOptions[this.state.artformIndex]}
                            organizationValue={this.organizationOptions[this.state.organizationIndex]}
                            phoneValidateError={this.state.phoneValidateError}
                            emailValidateError={this.state.emailValidateError}
                        />
                        <Button onClick={() => this.setState({showModal:!this.state.showModal})} variant="outline-primary" style={{width:'100%', marginTop:20}} className={globalClasses.outlinePrimaryBtn}>
                            Add custom fields
                        </Button>
                    </div>
                </div>
                <Modal
                    title={'Add custom field'} 
                    show={this.state.showModal}
                    body={this.customInput()}
                    valid={this.state.fileTypeIndex !== "" && this.state.fieldTitle !== ""}
                    btnRight="Add"
                    onSubmit={() => this.handleAddCancelCustomField('add')}
                    onHide={() => this.handleAddCancelCustomField('cancel')}
                />
            </motion.div>
        )
    }
} 

export default withAlert()(CreateArtist); 