import React from 'react'; 
import { useLocation } from 'react-router-dom';
import { Provider as AlertProvider } from 'react-alert';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as nearAPI from "near-api-js";

import { helpers } from './constants'
import configs from './configs';
import Routes from './routes';
import Sidebar from './components/sidebar/Sidebar';
import classes from './App.module.css';

export const WalletContext = React.createContext();


function App() {
  
  const { connect, WalletConnection } = nearAPI;

  const [wallet, setWallet] = React.useState(null);
  const [isWalletSignedIn, setIsWalletSignedIn] = React.useState(false);
  const location = useLocation();

  const contextValue =  {
    wallet: wallet,
    setWallet: setWallet,
    isWalletSignedIn: isWalletSignedIn,
    setIsWalletSignedIn: setIsWalletSignedIn,
  }

  const adjustmentWidth = 
  (location.pathname === "/404" || location.pathname === "/login") ?
  "100%" : "94%";

  // near wallet connection
  React.useEffect(() => {

    connect(configs.walletConfig)
    .then(res => {

      const wallet = new WalletConnection(res);

      if(wallet.isSignedIn()) {
        setIsWalletSignedIn(true);
      }
      setWallet(wallet);
    })
    .catch(err => {
      console.log(err);
    });

  }, []);


  return <AlertProvider template={helpers.AlertTemplate} {...configs.options}>
    <WalletContext.Provider value={contextValue}>
      <div className={classes.appContainer}>
        {(location.pathname === "/404" || location.pathname === "/login") ?
          null :
          <div className={classes.colOne}>
            <Sidebar/> 
          </div>}
        <div style={{width:adjustmentWidth}} className={classes.colTwo}>
          <Routes/>
        </div>
      </div>
    </WalletContext.Provider>
  </AlertProvider>
}

export default App;

