import React, { useEffect, useState } from 'react';
import { Row, Col, Button } from 'react-bootstrap';
import { useAlert } from 'react-alert';
import { motion } from "framer-motion";

import pdf from '../../assets/images/pdf.png';
import doc from '../../assets/images/doc.png';
import video from '../../assets/images/video.png';
import Modal from '../../components/uiElements/Modal';
import Loading from '../../components/uiElements/Loading';
import { animateConfigs, colors, helpers } from '../../constants';
import { _deleteOrganization, _getOneOrganization } from '../../services/axios/api';
import { ChevronLeft, Instagram, Facebook, Globe, Wallet, LocationIcon } from '../../components/svgComponents';
import classes from './artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';
import ArtistCard from '../../components/ArtistCard';

export default function ViewOrganization({ history, match: { params } }) {

    const [loading, setLoading] = useState(true);
    const [organization, setOrganization] = useState(""); 
    const [showModal, setShowModal] = useState(false);
    const [deleteLoading, setDeleteLoading] = useState(false);
    
    const alert = useAlert();

    useEffect(() => {

        _getOneOrganization(params.id)
        .then(({ data }) => {
            console.log(data);
            setOrganization(data.organization);
            setLoading(false);
        })
        .catch(err => {
            console.log(err);
            setLoading(false);
        })

        return () => {
                
        }

    }, [])

    function handleDelete() {
        setDeleteLoading(true);
        _deleteOrganization(params.id)
        .then(res => {
            setDeleteLoading(false);
            history.push('/organization');
            alert.success('Organization deleted successfully!', {timeout:2000});
        })
        .catch(err => {
            console.log(err);
            setDeleteLoading(false);
            alert.error('Failed to delete!', {timeout:2000})
        })
    }

    const renderArtists = (artists) => {
        return artists.map((artist) => {
            const date = new Date(artist.createdAt);
            const parsedDate = `${date.getDate()}-${date.getMonth() + 1}-${date.getFullYear()}`
            return <Col key={artist._id} lg={6}>
                <ArtistCard
                    image={artist?.image}
                    name={artist?.name}
                    date={parsedDate}
                    showBadge={false}
                    bottomLeftTitle={
                    <div style={{fontSize:14, display:'flex', alignItems:'center', marginLeft:-5, marginRight:15}}>
                        <div><LocationIcon size={20} color={'rgba(0, 25, 76, 0.4)'}/></div>
                        <div style={{opacity: 0.8, marginLeft: "2px", whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis", width: "100px"}}>{artist?.state}</div>
                    </div>}
                    onClick={() => history.push(`/viewartist/${artist._id}`)}
                />
            </Col>
        })
    }

    const emptyState = () => {
        return <div style={{textAlign:'center', marginTop:'25%'}}>
            <div style={{fontSize:26, opacity:0.7}}>No artists added to this organization</div>
            <Button onClick={() => history.push('/createartist')} className={globalClasses.outlinePrimaryBtn} style={{height:45, width:115, borderRadius:5, marginTop:10}}>
                New Artist
            </Button>
        </div>
    }

    const renderFiles = (cI) => {

        if(cI.fileType === "video") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='100px' width= '100px' style={{borderRadius:10, marginLeft:-10, cursor:'pointer'}} src={video} alt="upload"/>
            </div>
        } else if(cI.fileType === "image") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{objectFit:'contain', cursor:'pointer'}} src={cI.uri ? cI.uri : cI.fileUrl} alt="upload"/>
            </div>
        } else if(cI.fileType === "document") {
            <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={doc} alt="upload"/>
            </div>
        } else if(cI.fileType === "pdf") {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <img onClick={() => helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={pdf} alt="upload"/>
            </div>
        } else {
            return <div>
                <div className={classes.subHeadings}>{cI.name}</div>
                <div>{cI.text ? cI.text : cI.date}</div> 
            </div>
        }
    }
    
    if(loading) {
        return <Loading/>;
    }
    
    return (
        <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
            <header className={globalClasses.flexRowSpace} style={{marginBottom: '40px'}}>
                <div onClick={() => history.goBack()} className={globalClasses.flexRow} style={{cursor:'pointer'}}>
                    <ChevronLeft size={16}/>
                    <span style={{fontWeight:600, fontSize:22, marginLeft:17, marginTop:-2}}>{organization.name}</span>
                </div>
                <div>
                    <Button onClick={() => setShowModal(true)} variant="outline-danger" className={globalClasses.outlineDangerBtn} style={{width:125}}>Delete Org</Button>
                    <Button onClick={() => history.push("/editorganization", organization)} variant="outline-primary" className={globalClasses.outlinePrimaryBtn} style={{width:125, marginLeft:10}}>Edit Details</Button>
                </div>
            </header>
            <Row>
                <Col lg={5}>
                    <img style={{height:170, borderRadius:7}} src={organization.image} alt="profile"/>
                    {organization.description ? 
                    <>
                    <div className={classes.subHeadings}>Description</div>
                    <div style={{width:'85%'}}>
                        {organization.description} 
                    </div>
                    </> : null}
                    {organization.email || organization.phone ? 
                    <>
                    <div className={classes.subHeadings}>Contact details</div>
                    <div> {organization.phone ? `+91 ${organization.phone}` : ""} |  <span style={{color:colors.nakshBlue}}>{organization.email ? organization.email : ""} {}</span></div> 
                    </> : null}
                    {organization.state ? 
                    <>
                    <div className={classes.subHeadings}>Location</div>
                    <div>{organization.state}</div> 
                    </> : null}
                    {organization.custom ? 
                        organization.custom.map(cI => {
                            return renderFiles(cI);
                        }) : null
                    }
                    {organization.website || organization.facebook || organization.instagram ?
                    <>
                    <div className={classes.subHeadings}>Social Media</div>
                    <div className={globalClasses.flexRowSpace} style={{width:'30%', marginTop:5}}>
                        {organization.website ? <span onClick={() => helpers.openInNewTab(organization.website)}><Globe/></span> : null}
                        {organization.instagram ? <span onClick={() => helpers.openInNewTab(organization.instagram)}><Instagram/></span> : null}
                        {organization.facebook ? <span onClick={() => helpers.openInNewTab(organization.facebook)}><Facebook/></span> : null}
                    </div>
                    </> : null}
                </Col>
                <Col lg={7} style={{borderLeft:'1.5px solid rgba(0, 25, 76, 0.1)'}}>
                    <div style={{display:'flex', alignItems:'center', marginTop:-10, justifyContent:'space-between'}}>
                        <div style={{fontWeight:600, fontSize:20}}>
                            Artists at {organization.name}.
                        </div>
                        {organization?.artists?.length !== 0 && <div>
                            <Button onClick={() => history.push('/createartist')} className={globalClasses.outlinePrimaryBtn} style={{height:45, width:115, borderRadius:5}}>
                                New Artist
                            </Button>
                        </div>}
                    </div>
                    <Row style={{marginTop:25}}>
                        { organization?.artists?.length !== 0 ?
                            renderArtists(organization.artists) :
                            emptyState() }
                    </Row>
                </Col>
            </Row>
            <Modal
                title={'Delete organisation'} 
                show={showModal}
                body={'Are you sure you want to delete this organisation? The artists would be retained under the artist tab.'}
                valid={true}
                btnRight={deleteLoading ? "Deleting..." : "Yes, delete"}
                btnLeft="No, cancel"
                onSubmit={handleDelete}
                onHide={() => setShowModal(false)}
            />
        </motion.div>
    )
}

