import React from 'react';
import { NavLink, useLocation, useHistory } from 'react-router-dom';

import Modal from '../uiElements/Modal';
import { removeToken } from '../../routes/authUtilities';
import { Image, Users, ExternalLink, SidebarArtform } from '../svgComponents';
import logo from '../../assets/images/Dlogo.svg';
import { colors } from '../../constants';
import classes from './sidebar.module.css';
import globalClasses from '../../constants/globalStyles.module.css';
import { WalletContext } from '../../App';
import configs from '../../configs';


function Sidebar(props) {

    const location = useLocation();
    const history = useHistory();

    const contextValue = React.useContext(WalletContext);

    const [changeIconColor, setChangeIconColor] = React.useState({
        icon1: colors.inActiveIcon,
        icon2: colors.inActiveIcon,
        icon3: colors.inActiveIcon,
        icon4: colors.inActiveIcon
    });
    const [show, setShow] = React.useState(false); 
    const [walletName, setWalletName] = React.useState("");
    const [walletModal, setWalletModal] = React.useState(false);

    React.useEffect(() => {
        changeActiveState();
        console.log(location.pathname, "location.pathname");
    }, [location.pathname]);

    React.useEffect(() => {    
        if(contextValue.isWalletSignedIn) {
            setWalletName(contextValue.wallet.getAccountId());
        }
    }, [contextValue.wallet]);

    function changeActiveState() {
        if(location.pathname === "/" || location.pathname === "/createartist") {
            handleMouseOver('icon1');
            changeInactive('icon2');
            changeInactive('icon3');
            changeInactive('icon4');
        } else if(location.pathname === "/nfts" || location.pathname === "/viewnft" || location.pathname === "/createnft") {
            handleMouseOver('icon2');
            changeInactive('icon1');
            changeInactive('icon3');
            changeInactive('icon4');
        } else if(location.pathname === "/artforms"){
            handleMouseOver('icon3');
            changeInactive('icon1');
            changeInactive('icon2');
            changeInactive('icon4');
        }
    }

    function changeInactive(icon) {
        setChangeIconColor(prevState => ({
            ...prevState,
            [icon]: colors.inActiveIcon
        }));
    }

    function handleMouseOver(icon) {
        setChangeIconColor(prevState => ({
            ...prevState,
            [icon]: colors.activeIcon
        }));
    }

    function handleMouseLeave(icon) {
        changeInactive(icon);
        changeActiveState();
    }

    function walletSignIn() {
        contextValue.wallet.requestSignIn({
            successUrl: configs.appUrl,
            failureUrl: `${configs.appUrl}/404`
        });
    };

    function walletSignOut() {
        setWalletModal(false)
        contextValue.wallet.signOut();
        setWalletName("");
        contextValue.setIsWalletSignedIn(false);
    };

    function handleLogout() {
        setShow(false);
        removeToken();
        history.push("/login");
    }


    return (
        <div className={classes.sidebarContainer}>
            <div className={classes.appLogoContainer}>
                <img src={logo} alt={'logo'}/>
            </div>
            <NavLink 
                exact={true} 
                to="/" 
                className={location.pathname === "/" || location.pathname === "/organization" ? classes.activeAppBarItems : classes.appBarItems}
                onMouseOver={() => handleMouseOver('icon1')} 
                onMouseLeave={() => handleMouseLeave('icon1')}
            >
                <Users size={17} color={changeIconColor.icon1}/>
                <div style={{marginLeft:20}}>Artists</div>
            </NavLink>
            <NavLink 
                to="/nfts" 
                className={location.pathname === "/nfts" || location.pathname === "/viewnft" || location.pathname === "/createnft" ? classes.activeAppBarItems : classes.appBarItems}  
                onMouseOver={() => handleMouseOver('icon2')} 
                onMouseLeave={() => handleMouseLeave('icon2')}
            >
                <Image size={17} color={changeIconColor.icon2}/>
                <div style={{marginLeft:20}}>NFTs</div>
            </NavLink>
            <NavLink 
                to="/artforms" 
                className={location.pathname === "/artforms" ? classes.activeAppBarItems : classes.appBarItems}  
                onMouseOver={() => handleMouseOver('icon3')} 
                onMouseLeave={() => handleMouseLeave('icon3')}
            >
                <SidebarArtform size={17} color={changeIconColor.icon3}/>
                <div style={{marginLeft:20}}>ArtForms</div>
            </NavLink>
            <div style={{margin:'10px 0px'}} className={globalClasses.underline}/>
            <div className={classes.appBarItems} activeClassName={classes.activeAppBarItems} onMouseOver={() => handleMouseOver('icon4')} onMouseLeave={() => handleMouseLeave('icon4')}>
                <div style={{marginRight:5, fontSize:16}}>Open Marketplace</div>
                <ExternalLink size={16} color={colors.inActiveIcon}/>
            </div>
            {walletName ?
                <div style={{paddingLeft:26, marginTop:10, background: 'rgba(221, 223, 245, 0.2)', paddingTop:10, paddingBottom:10}}>
                    <div title={walletName} className={classes.walletTitle}>{walletName}</div>
                    <div onClick={() => setWalletModal(true)} style={{color:"#fff", cursor:"pointer"}}>Disconnect</div>
                </div> :
                <div onClick={walletSignIn} className={classes.appBarItems} activeClassName={classes.activeAppBarItems} onMouseOver={() => handleMouseOver('icon5')} onMouseLeave={() => handleMouseLeave('icon5')}>
                    <div style={{marginRight:5, fontSize:16}}>Connect Wallet</div>
                    <ExternalLink size={16} color={colors.inActiveIcon}/>
                </div>}
            <div style={{background:"#292A34", padding:"13px 0px 13px 25px", bottom:0, position:'absolute', width:'100%'}}>
                <div style={{color:"#A4A6B3", fontSize:12}}>{localStorage.getItem("adminEmail")}</div>
                <div onClick={() => setShow(true)} style={{color:"#fff", fontSize:15, marginTop:3, cursor:'pointer'}}>Logout</div>
            </div>
            <Modal
                show={show}
                onHide={() => setShow(false)}
                title='Are you sure. You want to logout?'
                btnLeft='No'
                btnRight='Yes, Logout'
                valid={true}
                onSubmit={handleLogout}
            />
            <Modal
                show={walletModal}
                onHide={() => setWalletModal(false)}
                title='Are you sure. You want to disconnect?'
                btnLeft='No'
                btnRight='Disconnect'
                valid={true}
                onSubmit={walletSignOut}
            />
        </div>
    )
}

export default Sidebar;
