import React from 'react';
import { motion } from "framer-motion"

import { Calendar } from '../components/svgComponents';
import Badge from '../components/uiElements/Badge';
import classes from '../pages/artists/artist.module.css';

export default function ArtistCard(props) {

    const { onClick, image, name, date, status, availableNo, bottomLeftTitle, showBadge } = props;

    return (
        <motion.div     
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }} 
            className={classes.artistCard}
            onClick={onClick}
        >
            <div>
                <img className={classes.cardThumbnail} src={image} alt={'artist'}/>
            </div>
            {showBadge && <Badge style={{position:'absolute', zIndex:0, top:10, right:10}}>
                <div style={{display:'flex'}}>
                    {availableNo !== null ? <div style={{marginRight:5}}>{availableNo}</div> : null}
                    <div>{status}</div>
                </div>
            </Badge>}
            <div className={classes.bottomHeading}>
                <div style={{fontWeight:600, marginBottom:2}}>{name}</div>
                <div style={{display:'flex', alignItems:'center', paddingBottom:3}}>
                    {bottomLeftTitle}
                    <div style={{fontSize:14, display:'flex', alignItems:'center'}}>
                        <div style={{marginTop:-3}}><Calendar/></div>
                        <div style={{opacity:0.8, marginLeft:2}}>{date}</div>
                    </div>
                </div> 
            </div>
        </motion.div>
    )
}

ArtistCard.defaultProps = {
    availableNo: null,
    showBadge: true
}
