import { Tick, Cross, CrossCircle } from '../components/svgComponents';
import { colors } from '../constants';

const readImage = (e) => {
    if (e.target.files) {
        const files = Array.from(e.target.files);
        return Promise.all(files.map(file => {
            return (new Promise((resolve,reject) => {
                const reader = new FileReader();
                reader.addEventListener('load', (ev) => {
                    resolve(ev.target.result);
                });
                reader.addEventListener('error', reject);
                reader.readAsDataURL(file);
            }));
        }))
    }
}

const AlertTemplate = ({ options, message, close }) => (
    <div 
        style={{
            fontSize:14, 
            marginTop:20, 
            borderRadius:5,
            color: options.type === 'success' ? '#3A974A' : colors.nakshRed, 
            background: options.type === 'success' ? '#CBF3D2' : '#FCD8D4', 
            padding:'9px 15px', 
            border:`1px solid ${options.type === 'success' ? '#3A974A' : colors.nakshRed}`,
            display:'flex',
            alignItems:'center'
        }}
    >
        <div>
            { options.type === 'success' ? <Tick size={27} color={'#3A974A'}/> : <CrossCircle size={27} color={colors.nakshRed}/>}
        </div>
        <div style={{margin:'0 15px 0 10px'}}>
            {message}
        </div>
        <div onClick={close}>
            <Cross size={18} color={options.type === 'success' ? '#3A974A' : colors.nakshRed}/>
        </div>
    </div>
)

const getOptionIndex = (value, optionsArray) => {
    return optionsArray.findIndex(option => option.value === value);
}

const openInNewTab = (url) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
    if (newWindow) newWindow.opener = null
}

function acceptedFormat(fileType) {

    if(fileType === "image") {
        return "image/x-png,image/gif,image/jpeg";
    } 
    else if (fileType === "video") {
        return "video/mp4,video/mpeg";
    } 
    else if (fileType === "pdf") {
        return "application/pdf";
    } 
    else if (fileType === "document") {
        return ".doc,.docx";
    }

}

const clearLocalStorage = () => {
    localStorage.removeItem("nftPrice");
    localStorage.removeItem("paramsId");
    localStorage.removeItem("primaryParamsId");
    localStorage.removeItem("nftPrice");
}

// eslint-disable-next-line import/no-anonymous-default-export
export default {
    readImage,
    AlertTemplate,
    getOptionIndex,
    openInNewTab,
    acceptedFormat,
    clearLocalStorage
}