import axiosAuth from './axiosAuth';
import axiosNoAuth from './axiosNoAuth';


// GET

export const _getAllArtforms = () => axiosAuth.get('/artform/all');

export const _getAllOrganizations = (params) => axiosAuth.get('/organization/all', {params});

export const _getOneOrganization = (id) => axiosAuth.get(`/organization/get?id=${id}`);

export const _getAllArtists = (params) => axiosAuth.get('/artist/all', {params});

export const _getOneArtist = (id) => axiosAuth.get(`/artist/get?id=${id}`);



// POST  

export const _getPresignedUrl = (data) => axiosAuth.post('/file/upload', data);

export const _postArtforms = (data) => axiosAuth.post('/artform/add', data);

export const _postOrganization = (data) => axiosAuth.post('/organization/add', data);

export const _postArtist = (data) => axiosAuth.post('/artist/add', data);

export const _sendOtp = (data) => axiosNoAuth.post('/account/login', data);

export const _verifyOtp = (data) => axiosNoAuth.post('/account/verify', data);

export const _uploadNft = (data) => axiosAuth.post('/nft/upload', data);



// PUT 

export const _uploadFileAws = (url, file, type) => (
    axiosNoAuth.put(url, file, {headers: {
        'Content-Type': type
    }})
) 


// PATCH

export const _updateArtform = (id, data) => axiosAuth.patch(`/artform/update?id=${id}`, data);

export const _updateOrganization = (id, data) => axiosAuth.patch(`/organization/update?id=${id}`, data);

export const _updateArtist = (id, data) => axiosAuth.patch(`/artist/update?id=${id}`, data);




// DELETE 

export const _deleteArtform = (id) => axiosAuth.delete(`/artform/delete?id=${id}`);

export const _deleteOrganization = (id) => axiosAuth.delete(`/organization/delete?id=${id}`);

export const _deActivateArtist = (id) => axiosAuth.delete(`/artist/deactivate?id=${id}`);


