import { utils } from 'near-api-js';
import uuid from 'react-uuid';
import configs from '../../configs';

import { _getOneArtist } from '../axios/api';


export default function NearHelperFunctions(contextValue, paramsId) {

    this.getAllListedNfts = async (allNfts) => {
        const res = await contextValue.wallet.account()
        .viewFunction(
            configs.nakshMarketWallet, 
            'get_sales_by_nft_contract_id', 
            { 
                nft_contract_id: configs.nakshContractWallet,
                from_index: "0", 
                limit: 1000 
            }
        )

        allNfts.map(nftItem => {
            const listedItem = res.find(t => t.token_id === nftItem.token_id);
            if(listedItem) {
                nftItem["listed"] = true;
                nftItem["price"] = `${listedItem.sale_conditions}N`;
            } else {
                nftItem["listed"] = false;
                nftItem["price"] = "NIL";
            }
        });

        if(paramsId) {

            const item = allNfts.find(item => item.token_id === paramsId);
            item.metadata['extra'] = JSON.parse(item.metadata.extra);

            const { data: { artist } } = await _getOneArtist(item.metadata.extra.artistId)
            item.metadata['extra'].artistName = artist.name;
           
            return item;

        }

        return allNfts;
    }

    this.getAllNfts = async () => {

        const res = await contextValue.wallet.account()
        .viewFunction(
            configs.nakshContractWallet, 
            'nft_tokens', 
            { 
                from_index: "0", 
                limit: 1000 
            }
        );

        const nftsWithPrice = await this.getAllListedNfts(res); // to get nft price

        return nftsWithPrice;

    };

    this.getOneNft = async () => {

        const oneNft = await this.getAllNfts();
        return oneNft;

    };

    this.nearListing = async (nftDetails) => {

        const msg = JSON.stringify({
            sale_conditions: localStorage.getItem("nftPrice")
        });
        const gas = 200000000000000;
        const attachedDeposit = utils.format.parseNearAmount("10");
        const FunctionCallOptions = {
            contractId: configs.nakshContractWallet,
            methodName: 'nft_approve',
            args: {
                account_id: configs.nakshMarketWallet,
                token_id: nftDetails.token_id,
                msg
            },
            gas,
            attachedDeposit
        };
        
        localStorage.removeItem("nftPrice");
        localStorage.removeItem("paramsId");
        
        contextValue.wallet.account().functionCall(FunctionCallOptions); // near redirection
    }

    this.nearStorage = async (nftPrice) => {
    
        const gas = 200000000000000;
        const attachedDeposit = utils.format.parseNearAmount("0.1");
        const FunctionCallOptions = {
            contractId: configs.nakshMarketWallet,
            methodName: 'storage_deposit',
            args: {},
            gas,
            attachedDeposit
        };
        
        localStorage.setItem("paramsId", paramsId);
        localStorage.setItem("primaryParamsId", paramsId);
        localStorage.setItem("nftPrice", nftPrice);

        contextValue.wallet.account().functionCall(FunctionCallOptions); // near redirection

    }

    this.updateNft = async (nftDetails, nftPrice) => {

        const gas = 200000000000000;
        const attachedDeposit = "1";
        const FunctionCallOptions = {
            contractId: configs.nakshMarketWallet,
            methodName: 'update_price',
            args: {
                nft_contract_id: configs.nakshContractWallet,
                token_id: nftDetails.token_id,
                price: nftPrice
            },
            gas,
            attachedDeposit
        };

        contextValue.wallet.account().functionCall(FunctionCallOptions); // near redirection
    }

    this.mintNft = async (metadata) => {

        const gas = 200000000000000;
        const attachedDeposit = utils.format.parseNearAmount("0.1");
        const perpetual_royalties = {
            "laksh.testnet": 500
        };

        const FunctionCallOptions = {
            contractId: configs.nakshContractWallet,
            methodName: 'nft_mint',
            args: {
                token_id: uuid(),
                metadata,
                perpetual_royalties
            },
            gas,
            attachedDeposit
        };

        contextValue.wallet.account().functionCall(FunctionCallOptions); // near redirection

    }

    this.getArtistsNfts = async () => {

    }

  }
  