import React from 'react';
import { Row, Col, Form } from 'react-bootstrap';
import Select from 'react-select';
import Autocomplete from 'react-google-autocomplete';


import pdf from '../assets/images/pdf.png';
import doc from '../assets/images/doc.png';
import video from '../assets/images/video.png';
import { Delete } from './svgComponents';
import classes from '../pages/artists/artist.module.css';
import globalClasses from '../constants/globalStyles.module.css';
import { helpers } from '../constants';

function OrganizationForm(props) {

    const { 
        customInputs, 
        onChangeText, 
        modifyInput, 
        instagram, 
        facebook, 
        website, 
        email, 
        phoneNumber, 
        description, 
        OrganizationName,
        setCity,
        stateOptions,
        onDropdownChange,
        stateValue,
        city,
        phoneValidateError,
        emailValidateError
    } = props;

    console.log(customInputs, 'customInputs');


    return ( 
        <React.Fragment>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Organizations name<span style={{color:'#FF4848'}}>*</span>
                    </Form.Label>
                    <Form.Control type="text" placeholder="Type here" onChange={(e) => onChangeText('name', e.target.value)} value={OrganizationName} />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <div className={globalClasses.flexRowSpace}>
                        <Form.Label className={globalClasses.label}>
                            Description
                        </Form.Label>
                        <div style={{fontSize:14, opacity:0.6}}>
                            0/400
                        </div>
                    </div>
                    <Form.Control as="textarea" style={{height:125}} placeholder="Type here" onChange={(e) => onChangeText('description', e.target.value)} value={description} />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        State
                    </Form.Label>
                    <Select
                        styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                        className="basic-single"
                        classNamePrefix="select"
                        defaultValue={"select"}
                        isClearable={false}
                        isSearchable={false}
                        name="color"
                        onChange={(e) => onDropdownChange('state', e)}
                        value={stateValue}
                        options={stateOptions}
                    />
                </Col>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        City/Town
                    </Form.Label>
                    <Autocomplete
                        value={city}
                        onChange={(e) => setCity(e.target.value)}
                        className={classes.input}
                        apiKey={"AIzaSyCSFIqgTq2F9Rd0537oU6kPBoe8bmOK7nw"}
                        options={{
                            types: ["(cities)"],
                            componentRestrictions: { country: "in"},
                        }}
                        onPlaceSelected={(place) => setCity(place.address_components[0].long_name)}
                        placeholder="Type here"
                    />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col style={{position:'relative'}}>
                    <Form.Label className={globalClasses.label}>
                        Phone Number
                    </Form.Label>
                    <Form.Control style={{paddingLeft:60, borderColor: phoneValidateError ? 'red' : '#ccc'}} type="text" maxlength="10" placeholder="Enter 10 digit number" onChange={(e) => onChangeText('phone', e.target.value)} value={phoneNumber}/>
                    <div className={classes.phoneNum}>+91</div>
                    {phoneValidateError && <div style={{color:'red'}}>enter a valid phone number</div>}
                </Col>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Email ID
                    </Form.Label>
                    <Form.Control style={{borderColor: emailValidateError ? 'red' : '#ccc'}} type="email" placeholder="Type here" onChange={(e) => onChangeText('email', e.target.value)} value={email} />
                    {emailValidateError && <div style={{color:'red'}}>enter a valid email</div>}
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Website
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" onChange={(e) => onChangeText('website', e.target.value)} value={website} />
                </Col>
            </Row>
            <Row style={{marginBottom:20}}>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Facebook
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" onChange={(e) => onChangeText('facebook', e.target.value)} value={facebook} />
                </Col>
                <Col>
                    <Form.Label className={globalClasses.label}>
                        Instagram
                    </Form.Label>
                    <Form.Control type="text" placeholder="Enter URL here" onChange={(e) => onChangeText('instagram', e.target.value)} value={instagram} />
                </Col>
            </Row>
            <Row style={{marginBottom:10}}>
                {customInputs.map((cI, i) => {
                return <React.Fragment key={i}>
                    <Col lg={11}>
                        <Form.Label style={{textTransform:'capitalize'}} className={globalClasses.label}>
                            {cI.name}
                        </Form.Label>
                        {cI.type === 0 ?
                        <Form.Control type="text" placeholder="Type here" onChange={(e) => modifyInput('edit', i, e.target.value)} value={cI.text}/> :
                        cI.type === 1 ?
                        <div style={{display:'flex', alignItems:'center'}}>
                            <label htmlFor={`UploadImg${i}`} style={{width:150, height:40, marginRight:15, cursor:'pointer', borderRadius:5}} className={globalClasses.primaryBtn}>
                                <div style={{color:'white', textAlign:'center', marginTop:7.2, fontSize:15, textTransform:'capitalize'}}>
                                    {`${cI.fileType} Upload`}
                                </div>
                                <input 
                                    onChange={(e) => modifyInput('editFile', i, e)} 
                                    id={`UploadImg${i}`} 
                                    hidden 
                                    type="file" 
                                    name="Pick an Image" 
                                    accept={helpers.acceptedFormat(cI.fileType)}
                                />
                            </label>
                            <div>
                                <div style={{display:'flex', marginRight:10}}>
                                    {cI.fileType === "video" && cI.fileKey !== "fileKey" ?
                                    <div className={globalClasses.flexRow}>
                                        <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='100px' width= '100px' style={{borderRadius:10, marginLeft:-10, cursor:'pointer'}} src={video} alt="upload"/>
                                        <div className={globalClasses.truncate} style={{width:200, marginLeft:-10}}>{cI.fileKey.name}</div>
                                    </div> :
                                    cI.fileType === "image" && cI.fileKey !== "fileKey"  ?
                                    <div className={globalClasses.flexRow}>
                                        <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{cursor:'pointer', objectFit:'contain'}} src={cI.uri ? cI.uri : cI.fileUrl} alt="upload"/>
                                        <div className={globalClasses.truncate} style={{width:200, marginLeft:15}}>{cI.fileKey.name}</div>
                                    </div> :
                                    cI.fileType === "document" && cI.fileKey !== "fileKey"  ?
                                    <div className={globalClasses.flexRow}>
                                        <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={doc} alt="upload"/>
                                        <div className={globalClasses.truncate} style={{width:200}}>{cI.fileKey.name}</div>
                                    </div> :
                                    cI.fileType === "pdf" && cI.fileKey !== "fileKey"  ?
                                    <div className={globalClasses.flexRow}>
                                        <img onClick={() => cI.uri ? null : helpers.openInNewTab(cI.fileUrl)} height='80px' width= '80px' style={{borderRadius:10, cursor:'pointer'}} src={pdf} alt="upload"/>
                                        <div className={globalClasses.truncate} style={{width:200}}>{cI.fileKey.name}</div>
                                    </div> : null} 
                                </div> 
                            </div>
                        </div> :
                        <div>
                            <label className={classes.datePicker} htmlFor="dateStyle">
                                <input id="dateStyle" type="date" value={cI.date} onChange={(e) => modifyInput('editDate', i, e.target.value)}/>
                            </label>
                        </div> }
                    </Col> 
                    <Col lg={1} onClick={() => modifyInput('delete', i)} style={{marginTop:35, marginBottom:40, cursor:'pointer'}}>
                        <Delete size={20}/>
                    </Col>
                </React.Fragment>})}
            </Row>
        </React.Fragment>
    )
}

export default OrganizationForm;
