import React, { Component, useContext } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import Select from 'react-select';
import { useAlert } from 'react-alert';
import { motion } from "framer-motion";
import * as nearAPI from "near-api-js";
import uuid from 'react-uuid';

import Modal from '../../components/uiElements/Modal';
import { helpers, animateConfigs } from '../../constants';
import { EditIcon, UploadOrganization, UploadNftPlaceholder } from '../../components/svgComponents';
import Loading from '../../components/uiElements/Loading';
import classes from '../artists/artist.module.css';
import globalClasses from '../../constants/globalStyles.module.css';
import NftForm from '../../components/NftForm';
import { WalletContext } from '../../App';
import { _getAllArtists, _uploadNft } from '../../services/axios/api';
import NearHelperFunctions from '../../services/nearHelperFunctions';

const { utils, Account } = nearAPI;

export class CreateNft extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            image: null, 
            nftImageRaw: null, 
            fieldTypeIndex: "",
            fileFormatIndex: "",
            fieldTitle: "",
            customInputs: [],
            name: "",
            artistName: "",
            artistId: "",
            description: "",
            materialMediumUsed: "",
            artist: "",
            quantity: 1,
            price: "",
            listOn: "",
            listInMarketPlace: false,
            loading: false,
        }

        this.fieldTypeOptions = [
            { value: 'text', label: 'Text Field' },
            { value: 'file', label: 'File upload' },
            { value: 'date', label: 'Date picker' }
        ]

        this.fileFormatOptions = [
            { value: 'image', label: 'Image' },
            { value: 'video', label: 'Video' },
            { value: 'document', label: 'Document' },
            { value: 'pdf', label: 'PDF' }
        ]

    }

    componentDidMount() {
        
        if(this.props.location?.state) {
            this.setRequiredData();
        }

        if(this.props.contextValue.wallet) {
            this.getAllArtists();
        }

    }

    componentDidUpdate(prevProps) {

        if(prevProps.contextValue.wallet !== this.props.contextValue.wallet) {     
            this.getAllArtists();  
        }
        
    }

    getAllArtists = () => {
        _getAllArtists({wallet: this.props.contextValue.wallet.getAccountId(), sortBy: 'createdAt', sort: -1})
        .then(res => {
            console.log(res);
            res.data.artists.length !== 0 && this.setState({artistName: res.data.artists[0].name, artistId: res.data.artists[0]._id})
        });
    }

    setRequiredData = () => {

    }

    onDropdownChange = (type, e) => {
        if(type === 'fieldType') {
            this.setState({fieldTypeIndex:helpers.getOptionIndex(e.value, this.fieldTypeOptions)});
        } else if(type === 'fileFormat') {
            this.setState({fileFormatIndex:helpers.getOptionIndex(e.value, this.fileFormatOptions)});
        }
    }

    handleAddCancelCustomField = (type) => {
        const { fieldTypeIndex, fileFormatIndex, fieldTitle, customInputs } = this.state;
        this.setState({showModal:false});
        if(type === 'add') {
            const copiedArr = [...customInputs];
            const data = {
                "name": fieldTitle
            }
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'text') {
                data["text"] = "";
                data["type"] = 0;
            }
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'file') {
                data["fileKey"] = "fileKey";
                data["fileType"] = this.fileFormatOptions[fileFormatIndex]?.value;
                data["type"] = 1;
            } 
            if(this.fieldTypeOptions[fieldTypeIndex].value === 'date') {
                data["date"] = "";
                data["type"] = 2;
            }
            copiedArr.push(data);             
            this.setState({fieldTypeIndex: "", fileFormatIndex: "", fieldTitle: "", customInputs: copiedArr});
        } else {

        }
    }

    handleModifyInput = async (type, i, value) => {
        
        const copiedArr = [...this.state.customInputs];
        if(type === 'delete') {
            const updatedArr = this.state.customInputs.filter((_, index) => i !== index);
            this.setState({customInputs:updatedArr});
        } else if(type === 'editFile') {
            const uri = await helpers.readImage(value);
            copiedArr[i].fileKey = value.target.files[0];
            copiedArr[i].uri = uri;
            this.setState({customInputs:copiedArr});
        } else if(type === 'editDate') {
            copiedArr[i].date = value;
            this.setState({customInputs:copiedArr});
        }
         else {
            copiedArr[i].text = value;
            this.setState({customInputs:copiedArr});
        }
    }

    onChangeText = (stateName, value) => {
        this.setState({[stateName]: value});
    }
     
    handleImage = async (e) => {
        const res = await helpers.readImage(e);
        this.setState({image:res[0], nftImageRaw: e.target.files[0]});
    }

    buildImgTag = () => {
        return <div style={{display:'flex', marginRight:10}}>
            <img height='180px' width= '180px' style={{borderRadius:100}} src={this.state.image} alt="upload"/>
        </div>
    } 

    uploadFilesToPinata = async () => {

        const fd = new FormData();
        fd.append("nftImage", this.state.nftImageRaw);

        if(this.state.customInputs.length !== 0) {
            this.state.customInputs.map(cI => {
                if(cI.type === 1) {
                    fd.append("custom", cI.fileKey);
                };
            });
        };
        
        const response = await _uploadNft(fd);
        return response;

    }

    setExtraData = (custom) => {
        
        const extraData = {};
        const customInputs = [];
        
        let i = 0;

        this.state.customInputs.map((cI) => {
            if(cI.type === 1) {
                delete cI.uri;
                delete cI.fileKey;
                cI['fileUrl'] = custom[i];
                i++;
                customInputs.push(cI);
            } else {
                customInputs.push(cI);
            }
        });
        
        extraData['artistName'] = this.state.artistName;
        extraData['artistId'] = this.state.artistId;

        if(customInputs.length !== 0) extraData['custom'] = customInputs;

        if(this.state.materialMediumUsed) {
            extraData['materialMediumUsed'] = this.state.materialMediumUsed;
        }
        
        return extraData;

    }
    
    handleNftMint = async () => { 

        if(!this.state.artistName) {
            this.props.alert.show(
                "Artist is required for creating NFT. You will be redirected to Artist creation page to create one", 
                {
                    timeout:6000,
                    onClose: () => this.props.history.push("/createartist")
                });
            return;
        }

        this.setState({loading:true});
        const pinataResponse = await this.uploadFilesToPinata();

        if(pinataResponse.status === 200) {

            const { nftImageUrl, custom } = pinataResponse.data;

            const extraData = this.setExtraData(custom);

            const metadata = {
                media: nftImageUrl,
                issued_at: Date.now(),
                title: this.state.name,
                description: this.state.description,
                extra: JSON.stringify(extraData)
            };
            
            const functions = new NearHelperFunctions(this.props.contextValue);
            functions.mintNft(metadata);  
            
           
        } else {
            this.setState({loading:false});
            alert("Failed to upload nft!");
        }
        
    }

    validateCustomInputs = (customInputs) => {
        const filterArr = customInputs.map((cI, i) => {
            if(cI?.text !== "" || cI?.file !== "file" || cI?.date !== "") {
                customInputs.splice(i, 1);
            }
        })
        // const filterArr = customInputs.map((cI, i) => cIc);
        return filterArr;
    }


    customInput = () => {
        return <Row style={{marginBottom:20}}>
            <Col lg={12}>
                <Form.Label className={globalClasses.label}>
                    Field Type<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Select
                    styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                    className="basic-single"
                    classNamePrefix="select"
                    defaultValue={"Select"}
                    onChange={(e) => this.onDropdownChange('fieldType', e)}
                    value={this.fieldTypeOptions[this.state.fieldTypeIndex]}
                    isClearable={false}
                    isSearchable={false}
                    name="color"
                    options={this.fieldTypeOptions}
                />
            </Col>
            {this.fieldTypeOptions[this.state.fieldTypeIndex]?.value === "file" ? <Col lg={12} style={{marginTop:15}}>
                <Form.Label className={globalClasses.label}>
                    Upload Format<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Select
                    styles={{valueContainer: () => ({height:42, paddingLeft:10, border:'none'}), indicatorSeparator: () => ({width:0})}}
                    className="basic-single"
                    classNamePrefix="select"
                    defaultValue={"Select"}
                    onChange={(e) => this.onDropdownChange('fileFormat', e)}
                    value={this.fileFormatOptions[this.state.fileFormatIndex]}
                    isClearable={false}
                    isSearchable={false}
                    name="color"
                    options={this.fileFormatOptions}
                />
            </Col> : null}
            <Col lg={12} style={{marginTop:15}}>
                <Form.Label className={globalClasses.label}>
                    Field Title<span style={{color:'#FF4848'}}>*</span>
                </Form.Label>
                <Form.Control type="text" placeholder="Type here" onChange={(e) => this.setState({fieldTitle: e.target.value})} value={this.state.fieldTitle} />
            </Col>
        </Row>
    }


    render() {

        const isValid = this.state.name && this.state.description && this.state.image;

        if(this.state.loading) {
            return <Loading/>;
        }

        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div className={globalClasses.flexRowSpace}> 
                    <div style={{fontWeight:600, fontSize:22}}>
                        {this.props.location?.state ? 'Edit' : 'New'} NFT
                    </div>
                    <div>
                        <Button onClick={() => this.props.history.push("/nfts")} variant="outline-primary" style={{width:125}} className={globalClasses.outlinePrimaryBtn}>
                            Cancel
                        </Button>
                        <Button onClick={isValid ? this.handleNftMint : null} style={{width:125, height:40, marginLeft:10, opacity: isValid ? 1 : 0.5}} className={globalClasses.primaryBtn}>
                            {this.props.location?.state ? 'Update NFT' : 'Mint NFT'}
                        </Button>
                    </div>
                </div>
                <div style={{marginTop:25, display:'flex', justifyContent:'space-between'}}>
                    <div className={globalClasses.containerCard} style={{width:'28%', textAlign:'center', padding: "50px", height: "fit-content"}}>
                        <label htmlFor="addImg" style={{position:'relative', cursor:'pointer'}}>
                            <input onChange={(e) => this.handleImage(e)} id="addImg" hidden type="file" name="Pick an Image" accept="image/x-png,image/gif,image/jpeg"/>
                            {this.state.image ?
                                this.buildImgTag() :
                            <UploadNftPlaceholder size={128}/>}
                            {this.state.image && <div className={classes.editIcon}>
                                <EditIcon size={20}/>
                            </div>}
                        </label>
                        <div style={{fontSize:16, fontWeight:600, opacity:0.7, marginTop:15}}>Upload your NFT here</div>
                    </div>
                    <div className={globalClasses.containerCard} style={{width:'70%'}}>
                        <NftForm
                            customInputs={this.state.customInputs}
                            modifyInput={this.handleModifyInput}
                            onChangeText={this.onChangeText}
                            name={this.state.name}
                            artistName={this.state.artistName}
                            description={this.state.description}
                            materialMediumUsed={this.state.materialMediumUsed}
                            artist={this.state.artist}
                            quantity={this.state.quantity}
                            listInMarketPlace={this.state.listInMarketPlace}
                            price={this.state.price}
                            listOn={this.listOn}
                            onDropdownChange={this.onDropdownChange}
                        />
                        <Button onClick={() => this.setState({showModal:!this.state.showModal})} variant="outline-primary" style={{width:'100%', marginTop:20}} className={globalClasses.outlinePrimaryBtn}>
                            Add custom fields
                        </Button>
                    </div>
                </div>
                <Modal
                    title={'Add custom field'} 
                    show={this.state.showModal}
                    body={this.customInput()}
                    valid={this.state.fieldTypeIndex !== "" && this.state.fieldTitle !== ""}
                    btnRight="Add"
                    onSubmit={() => this.handleAddCancelCustomField('add')}
                    onHide={() => this.handleAddCancelCustomField('cancel')}
                />
            </motion.div>
        )
    }
} 


function CreateNftWrapper(props) {

    const contextValue = useContext(WalletContext);
    const alert = useAlert();

    return <CreateNft 
        contextValue={contextValue}
        history={props.history}
        alert={alert}
    />;
}

export default (CreateNftWrapper);

