import React from 'react';
import { Modal, Spinner } from 'react-bootstrap';

import './loading.css';


export default function Loading(props) {
    return (
        <Modal dialogClassName="loading" show={true} {...props} size="lg" aria-labelledby="contained-modal-title-vcenter" centered>
            <div style={{width:100, textAlign:'center', padding:5, marginLeft:5}}>
                <div className="sk-folding-cube">
                    <div className="sk-cube1 sk-cube"></div>
                    <div className="sk-cube2 sk-cube"></div>
                    <div className="sk-cube4 sk-cube"></div>
                    <div className="sk-cube3 sk-cube"></div>
                </div>
            </div>
            <div className="text">Please wait...</div>
        </Modal>
    )
}
