import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';

import { isLogin } from './authUtilities';
import PrivateRoute from './PrivateRoute';
import PublicRoute from './PublicRoute';
import Artists from '../pages/artists/Artists';
import CreateArtist from '../pages/artists/CreateArtist';
import CreateNft from '../pages/nft/CreateNft';
import ViewArtist from '../pages/artists/ViewArtist';
import ViewNft from '../pages/artists/ViewNft';
import Artforms from '../pages/artforms/Artforms';
import Nft from '../pages/nft/Nft';
import NotFound from '../pages/NotFound';
import CreateOrganization from '../pages/artists/CreateOrganization';
import ViewOrganization from '../pages/artists/ViewOrganization';
import Login from '../pages/authentication/Login';

export default function Routes() {
    return (
        <Switch>
            <PrivateRoute path="/" component={Artists} exact/>
            <PublicRoute path="/login" component={Login} restricted={isLogin()} />
            <PrivateRoute path="/organization" component={Artists}/>
            <PrivateRoute path="/createartist" component={CreateArtist}/>
            <PrivateRoute path="/createnft" component={CreateNft}/>
            <PrivateRoute path="/createorganization" component={CreateOrganization}/>
            <PrivateRoute path="/viewartist/:id" component={ViewArtist}/>
            <PrivateRoute path="/viewnft/:id" component={ViewNft}/>
            <PrivateRoute path="/vieworganization/:id" component={ViewOrganization}/>
            <PrivateRoute path="/editartist" component={CreateArtist}/>
            <PrivateRoute path="/editorganization" component={CreateOrganization}/>
            <PrivateRoute path="/artforms" component={Artforms}/>
            <PrivateRoute path="/nfts" component={Nft}/>
            <PublicRoute path='/404' component={NotFound}/>
            <Redirect from='*' to='/404' exact/>
        </Switch>
    )
}

