import React, { Component, createRef } from 'react';
import { Row, Col, Button, Form } from 'react-bootstrap';
import { withAlert } from 'react-alert';
import { motion } from "framer-motion";
import { debounce } from "lodash";

import { _deleteArtform, _getAllArtforms, _postArtforms, _updateArtform } from '../../services/axios/api';
import Modal from '../../components/uiElements/Modal';
import { colors, animateConfigs } from '../../constants';
import { EditDash, Delete } from '../../components/svgComponents';
import Loading from '../../components/uiElements/Loading';
import classes from './artforms.module.css';
import globalClasses from '../../constants/globalStyles.module.css';


class Artforms extends Component {

    constructor(props) {
        super(props);
        this.state = {
            status: 'add',
            artforms: [],
            artformInputs: [{value:""}],
            showModal: false,
            addingStatus: false,
            loading: false,
        };
        this.currentArtform = createRef().current;
    }   

    componentDidMount() {
        this.setState({loading:true});
        this.artFormsGet();
    }

    artFormsGet = debounce(() => {
        _getAllArtforms()
        .then(res => {
            console.log(res);
            this.setState({loading:false, artforms:res.data.artforms});
        })
        .catch(err => {
            console.log(err);
            this.setState({loading:false});
        })
    },0);

    handleAddArtform = () => {
        this.setState({addingStatus: true});
        const data = {
            "artforms": this.state.artformInputs.map(input => input.value)
        }
        _postArtforms(data)
        .then(res => {
            console.log(res, 'add response')
            this.artFormsGet();
            this.setState(
                {addingStatus:false, showModal:false, loading:true, artformInputs: [{value:""}]},
                () => this.handleError(res)
            );

        })
        .catch(err => {
            console.log(err);
            this.props.alert.error(err.response.data.error, {timeout:2000})
            this.setState({addingStatus: false});
        })
    }

    handleEditArtform = () => {
        this.setState({addingStatus: true});
        const data = {
            "name": this.state.artformInputs[0].value
        }
        _updateArtform(this.currentArtform._id, data)
        .then(res => {
            this.artFormsGet();
            this.setState({addingStatus:false, showModal:false, loading:true});
            this.props.alert.success('updated successfully!', {timeout:2000})
        })
        .catch(err => {
            this.setState(
                {addingStatus: false, showModal:false},
                () => this.props.alert.error(err.response.data.error, {timeout:2000})
            );
        })
    }

    handleDeleteArtform = () => {
        this.setState({addingStatus: true});
        _deleteArtform(this.currentArtform._id)
        .then(res => {
            console.log(res);
            this.artFormsGet();
            this.setState(
                {addingStatus:false, showModal:false, loading:true},
                () => this.props.alert.success('Deleted successfully!', {timeout:2000})
            );
        })
        .catch(err => {
            this.props.alert.error(err.response.data.error, {timeout:2000})
            this.setState({addingStatus: false, showModal:false});
        })
    }

    handleError = (res) => {
        if(res.status === 201) {
            res.data.result.map(msg => {
                msg.success ? 
                this.props.alert.success(msg.success, {timeout:2000}) : 
                this.props.alert.error(msg.error, {timeout:2000})
            }) 
        }
    }

    onOpen = (type, artform) => {
        this.currentArtform = artform;
        if(type === 'edit') {
            return this.setState({status:type, artformInputs: [{value: artform.name}], showModal:true}); 
        } else if(type === 'delete') {
            return this.setState({status:type, showModal:true}); 
        }
        this.setState({status:type, showModal:true});
    }

    onHide = () => {
        this.setState({artformInputs: [{value:""}], showModal:false});
    }

    handleChange = (e, index) => {
        const copiedArr = [...this.state.artformInputs];
        copiedArr[index].value = e.target.value;
        this.setState({artformInputs: copiedArr});
    }

    addInput = () => {
        if(this.state.artformInputs.length < 5) {
            const copiedArr = [...this.state.artformInputs];
            copiedArr.push({value: ""});
            this.setState({artformInputs: copiedArr});
        }
    }

    renderArtforms = () => {
        return this.state.artforms.map((artform, index) => {
            return <Col lg={3} key={index}>
                <div className={classes.artformStyle}>
                    <div>{`${artform.name}${" "}(${artform.totalArtists})`}</div>
                    <div style={{display:'flex'}}>
                        <div style={{marginRight:14}} onClick={() => this.onOpen('edit', artform)}>
                            <EditDash size={24}/>
                        </div>
                        <div onClick={() => this.onOpen('delete', artform)}>
                            <Delete size={24}/>  
                        </div>
                    </div>
                </div>
            </Col>
        })
    }

    customInput = () => {
        return <div>
            {this.state.artformInputs.map((artformInput, index) => {
                return  <Row style={{marginBottom:20}} key={index}>
                <Col lg={12}>
                    <Form.Label className={globalClasses.label}>
                        Artform Name 0{index+1}<span style={{color:'#FF4848'}}>*</span>
                    </Form.Label>
                    <Form.Control type="text" placeholder="Type here" onChange={(e) => this.handleChange(e, index)} value={artformInput.value} />
                </Col>
            </Row>})}
            {this.state.status === 'add' ? 
            <div onClick={this.addInput} style={{color:colors.nakshBlue, fontWeight:600, cursor:'pointer', opacity: this.state.artformInputs.length < 5 ? 1 : 0.5}}>
                <span style={{fontSize:20}}>+</span> Add new artform
            </div> : null}
        </div>
    }

    deleteContent = () => {
        return <div style={{fontWeight:600, fontSize:18, marginBottom:10}}>
            Are you sure want to delete this Artform?
        </div>
    }


    render() {

        if(this.state.loading) {
            return <Loading/>;
        }
    
        return (
            <motion.div variants={animateConfigs.fadeInOut} initial="hidden" animate="show">
                <div style={{marginBottom:50}} className={globalClasses.flexRowSpace}> 
                    <div style={{fontWeight:600, fontSize:22}}>Manage Artforms</div>
                    <Button onClick={() => this.onOpen('add')} style={{width:125, height:40, marginLeft:10}} className={globalClasses.primaryBtn}>
                        New Artform
                    </Button>
                </div>
                <Row>
                    { this.state.artforms.length === 0 ? 
                        <div className={classes.emptyPlaceholder}>Add Artforms</div> :
                        this.renderArtforms() }
                </Row>
                <Modal
                    title={this.state.status === 'add' ? 'Add artform' : this.state.status === 'edit' ? 'Update artform' : 'Delete artform'} 
                    show={this.state.showModal}
                    body={this.state.status === 'delete' ? this.deleteContent() : this.customInput()}
                    btnRight={this.state.addingStatus ? "loading.." : this.state.status === 'add' ? 'Add' : this.state.status === 'edit' ? 'Update' : 'Delete'}
                    onHide={this.onHide}
                    onSubmit={this.state.status === 'add' ? this.handleAddArtform : this.state.status === 'edit' ? this.handleEditArtform : this.handleDeleteArtform}
                    valid={this.state.status === 'delete' ? true : this.state.artformInputs.every(artForm => artForm.value !== "") ? true : false}
                />
            </motion.div>
        )
    }
}

export default withAlert()(Artforms); 